/* eslint-disable max-lines */
import { Component, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';

import {
  AppConfigService,
  CustomValidators,
  EntitiesUtils,
  FormUtils,
  IEditUserRequest,
  ILanguage,
  IUser,
  LANGUAGES_OPTIONS,
  LoadingService,
  MessagingService,
  PLATFORM_ENUM,
  USER_ROLES_ENUM,
  UserService,
} from '@aft-core';

@Component({
  selector: 'aft-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit, OnDestroy {
  profileIsLoading: boolean = false;
  form: FormGroup;
  originalData: IUser | undefined;
  profileData: IUser | undefined;
  isEditMode: boolean = false;
  isBackofficeProfile: boolean = false;
  languages = LANGUAGES_OPTIONS;
  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _fb: FormBuilder,
    private _loadingService: LoadingService,
    private _userService: UserService,
    private _messagingService: MessagingService,
    private _translateService: TranslateService,
    private _appConfigService: AppConfigService
  ) {
    this.isBackofficeProfile = this._appConfigService.appConfig.platform === PLATFORM_ENUM.BACKOFFICE;

    const basicFromGroup = {
      firstName: ['', [Validators.required, CustomValidators.Name]],
      lastName: ['', [Validators.required, CustomValidators.Name]],
      email: ['', [Validators.required, CustomValidators.Email]],
      phoneNumber: ['', [Validators.required, CustomValidators.Cellphone]],
      country: [''],
      city: [''],
    };

    if (this.isBackofficeProfile) {
      this.form = this._fb.group(basicFromGroup);
    } else {
      this.form = this._fb.group({
        ...basicFromGroup,
        organization: [''],
        webSite: [''],
        workplace: [''],
        linkedinProfileURL: [''],
        description: [''],
        languages: [],
      });
    }
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this._loadingService.loading$.subscribe(isLoading => {
        this.profileIsLoading = isLoading;
      }),
      this._userService.loadedProfile$.subscribe((user: IUser | undefined) => {
        this.originalData = user;
        this.profileData = { ...this.originalData } as IUser;
        this.loadFormData(this.profileData);
      })
    );
    if (this.isBackofficeProfile) {
      this._userService.getBackofficeProfile();
    } else {
      this._userService.getScoutProfile();
    }
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }

  get firstNameCtrl(): AbstractControl {
    return this.form.controls.firstName;
  }

  get lastNameCtrl(): AbstractControl {
    return this.form.controls.lastName;
  }

  get emailCtrl(): AbstractControl {
    return this.form.controls.email;
  }

  get phoneNumberCtrl(): AbstractControl {
    return this.form.controls.phoneNumber;
  }

  get languagesCtrl(): AbstractControl {
    return this.form.controls.languages;
  }

  // Sets form with profile data
  loadFormData(profileData: IUser): void {
    this.form.controls.firstName.setValue(profileData?.firstName);
    this.form.controls.lastName.setValue(profileData?.lastName);
    this.form.controls.email.setValue(profileData?.email);
    this.form.controls.phoneNumber.setValue(profileData?.phoneNumber);
    this.form.controls.country.setValue(profileData?.country);
    this.form.controls.city.setValue(profileData?.city);

    if (!this.isBackofficeProfile) {
      this.form.controls.organization.setValue(profileData?.organization);
      this.form.controls.webSite.setValue(profileData?.webSite);
      this.form.controls.workplace.setValue(profileData?.workplace);
      this.form.controls.linkedinProfileURL.setValue(profileData?.linkedinProfileURL);
      this.form.controls.description.setValue(profileData?.description);

      this.form.controls.languages.setValue(
        profileData?.languages?.map((lang: ILanguage) => {
          return EntitiesUtils.findLangByName(lang.name);
        })
      );
    }

    if (!this.isEditMode) {
      this.form.disable();
      this.emailCtrl.disable();
    }
  }

  // Swaps edit mode and saves the info upon finishing edit
  changeEditMode(): void {
    if (!this.isEditMode) {
      this.isEditMode = !this.isEditMode;
      this.form.enable();
      this.emailCtrl.disable();
    } else if (this.form.valid) {
      // Edit mode disabled, save data
      let profile = this.form.value as IEditUserRequest;
      profile.firstName = profile.firstName?.trim();
      profile.lastName = profile.lastName?.trim();
      delete profile.email;

      if (this.isBackofficeProfile) {
        this._userService.setBackofficeProfile(profile);
      } else {
        profile.languages = this.languagesCtrl.value?.map((lang: ILanguage) => ({
          name: lang.name,
        }));
        this._userService.setScoutProfile(profile);
      }

      this.isEditMode = !this.isEditMode;
      this.form.disable();
      this.emailCtrl.disable();
    } else {
      FormUtils.SubmitAllControls(this.form);
      this._messagingService.error('PROFILE.ERROR_SAVE_TITLE', 'PROFILE.ERROR_SAVE_MESSAGE');
    }
  }

  // Cancels edit mode and resets profile info
  cancelEditMode(): void {
    this.isEditMode = false;
    this.loadFormData(this.originalData as IUser);
  }

  // Returns Role translation key
  getRoleLabel(): string {
    const role = this.profileData?.role;
    switch (role) {
      case USER_ROLES_ENUM.ADMIN:
        return this._translateService.instant('GENERAL.ROLES.ADMIN');
      default:
        return this._translateService.instant('GENERAL.ROLES.SCOUT');
    }
  }

  // Returns the profile url or default
  resolveUsrPhotoSrc(): string {
    return this.profileData?.imageURL || 'assets/images/pictures/profile_default.png';
  }
}
